import React, { useEffect, useState } from 'react'
import Employees from './Employees'
import EmpInvestmentFilters from './EmpInvestmentFilters'
import { pagination } from '../../../utils/Constants.config'
import { useDispatch, useSelector } from 'react-redux'
import { employeeActions } from '../../../core/resource/actions'

const EmployeeInvestments = () => {
    const dispatch = useDispatch()

    const {
        getAllEmployeeListPending,
        getAllEmployeeListRes,
        getAllEmployeeListError,

    } = useSelector(state => state.employee)
    const [currentPage, setCurrentPage] = useState(1);
    const [allEmployeesData, setAllEmployeesData] = useState('')
    useEffect(() => {
        getAllEmployeesList()
    }, [])

    const getAllEmployeesList = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
        }
        dispatch(employeeActions.getAllEmployeeListRequest(params));
    }

    useEffect(() => {
        if (!getAllEmployeeListPending) {
            if (getAllEmployeeListError) {
                return;
            }
            if (getAllEmployeeListRes) {
                if (getAllEmployeeListRes?.code == 200) {
                    const employeeListResponse = getAllEmployeeListRes?.data
                    setAllEmployeesData(employeeListResponse)
                }
            }
        }
    }, [
        getAllEmployeeListRes,
        getAllEmployeeListError,
        getAllEmployeeListPending
    ]);

    return (
        <>
            <EmpInvestmentFilters />
            <div className='cardBox mt-3'>
                <div className=''>
                    <div className="d-flex justify-content-between align-items-center mb-2">
                        <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Employee's</h3>
                    </div>
                    <Employees
                        allEmployees={allEmployeesData} 
                    />
                </div>
            </div>
        </>
    )
}

export default EmployeeInvestments

