import React, { useMemo } from 'react'
import { CustomTable } from '../../reuseableComponents';
import { getBadgeColor } from '../../../utils/common';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { payrollActions } from '../../../core/resource/actions';

const Employees = (props) => {
    const dispatch =useDispatch()    
    const {currentPage,setCurrentPage ,allEmployees} = props
    const {
        getEmployeeDetailsByPayDaysPending,
    } = useSelector(state => state.payroll)
    const employeeField = [
        { key: 'sn', label: 'S.N' },
        { key: 'emp_id', label: 'Emp Id' },
        { key: 'name', label: 'Full Name' },
        { key: 'account_status', label: 'Account Status' },
        { key: 'action', label: 'Action' }
    ];
    const setSelectedEmpData =(data)=>{
        dispatch(payrollActions.selectedEmployeeData(data))
    }
    
    const employeeScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        account_status: item =>(
            <span
            className={`badge ${getBadgeColor(item.account_status)}`}
          >
            {item.account_status}
          </span>
        ) ,
        

        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <Link className="dropdown-item" to={`investments/${item?.id}`}
                        onClick={() => setSelectedEmpData(item)} 
                        >
                            Add Investment</Link>
                    </li>
                    
                </ul>
            </div>
        )
    }), [allEmployees,currentPage]);
    return (
        <>
            <CustomTable
                data={allEmployees?.data}
                fields={employeeField}
                isLoading={getEmployeeDetailsByPayDaysPending}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={allEmployees?.totalPages}
                // tableName={""}
                // onSearch={handleSearch}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={employeeScopedSlots}
            />
        </>
    )
}

export default Employees