
import React, { useState } from 'react'
import { useCustomTranslation } from '../../../translation'
import { Icon ,Label} from '../../commanComponent'
import CustomSelect from '../../commanComponent/CustomSelect'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { employeeIDs } from '../../../utils/common'

const EmpInvestmentFilters = (props) => {
    const {
        allEmployeeList,
        allDepartments,
        allDesignations,
        handleDepartmentByDesignation,
    } = props
    const { t } = useCustomTranslation()
    const [filterFormData, setFilterFormData] = useState({
        department: null,
        designation: null,
        employees:'',
    })

    const handleFilterFormData = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };


        if (name === "department") {
            value && value?.value && handleDepartmentByDesignation(value?.value)
            setFilterFormData(prevState => ({
                ...prevState,
                ["designation"]: null,
            })
            );
        }
        setFilterFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });
    }
    const handleFilterEmployees =()=>{
        const filterFields={};

        if(filterFormData?.department?.value)  filterFields["department_id"] = filterFormData?.department?.value;
        if(filterFormData?.designation?.value)  filterFields["designation_id"] = filterFormData?.designation?.value;
        if(filterFormData?.employees?.length>0) filterFields["employee_ids"]=employeeIDs(filterFormData?.employees)
    }
    
    return (
        <>
            <div className="formPanel cardBox">
                <div className="row">

                    <div className="col-md-4">
                        <div className="form-group mb-2">
                            <Label text={t("labels.department")} />
                            <CustomSelect
                                data={allDepartments}
                                placeholder={t("placeholders.selectDepartment")}
                                value={filterFormData?.department || ''}
                                handleChange={(selectedOption) => handleFilterFormData(selectedOption, "department")}
                                name="department"
                            />

                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="form-group mb-2">
                            <Label text={t("labels.designation")} />
                            <CustomSelect
                                data={allDesignations}
                                placeholder={t("placeholders.selectDesignation")}
                                value={filterFormData?.designation || ''}
                                handleChange={(selectedOption) => handleFilterFormData(selectedOption, "designation")}
                                name="designation"
                                isDisabled={!(filterFormData?.department) ? true : false}
                            />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="form-group mb-2">
                            <Label text={t("labels.employees")} />
                            <CustomSelect
                                data={allEmployeeList}
                                placeholder={t("placeholders.employees")}
                                value={filterFormData?.employees || ''}
                                handleChange={(selectedOption) => handleFilterFormData(selectedOption, "employees")}
                                name="employees"
                                isMulti={true}
                                selectAllText={t("labels.selectAll")}
                            />

                        </div>
                    </div>
                </div>
                <div className='d-flex justify-content-end'>

                    <CustomButton
                        classNames={"btn btn-primary gradientBg"}
                        handleClick={() => handleFilterEmployees()}
                        text={t("btns.filter")}
                        icon={<Icon className={"fa fa-search"}/>}
                    />
                </div>



            </div>
        </>
    )
}

export default EmpInvestmentFilters
